<template>
  <div id="note">
    <head-top title="笔记">
      <router-link slot="right" class="head-right" to="/editNote">
        <i class="iconfont icon-add"></i>
      </router-link>
    </head-top>

    <div class="search-input">
      <van-search placeholder="请输入搜索关键词" v-model="value" @search="onSearch" />
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <div class="main" v-if="searchData.length>0">
        <div class="item" v-for="(item, k) in searchData" :key="k" @click="showDetail(item)">
          <div class="item_operate" @click="dataDelete($event,item)">
            <van-icon name="delete" size="18px" color="#f56c6c" />
          </div>
          <h3 class="title">{{item['SNTtile']||'缺少标题'}}</h3>
          <r-quill-editor ref="REditor" :funType="funType" :content="item['SNContent']"
            :is-class="true" :is-edit="false" :max-height="null">
          </r-quill-editor>
          <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        </div>
        <div class="list_end">
          再也没有了~
        </div>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import NoteList from "@/components/r-note";
import RQuillEditor from "@/components/r-quill-editor";

// import "quill/dist/quill.snow.css";
import { Dialog } from "vant";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    NoteList,
    RQuillEditor,
    [Dialog.Component.name]: Dialog.Component
  },
  props: {},
  data() {
    return {
      isLoading: false,
      dataList: [],
      form: {
        CIID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID
      },
      error: false,
      finished: false,
      pno: 0,
      pageSize: 100,
      total: 0,
      funType: ModulePicture.StudentNote,
      value: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.onSearch();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      if (this.pno === 0 || this.total > this.pno * this.pageSize) {
        this.pno++;
      } else {
        this.isLoading = false;
        this.finished = true;
        return;
      }
      let reqData = {
        CIID: this.form.CIID,
        studentID: this.$store.getters.studentID,
        psize: this.pageSize,
        pno: this.pno
      };
      this.$gatewayHttp.note
        .getList(reqData)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              if (a.CreateTime > b.CreateTime) {
                return -1;
              } else {
                return 1;
              }
            });
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          if (res.RowsCount) this.total = res.RowsCount;
          if (this.total <= this.pno * this.pageSize) this.finished = true;
          this.isLoading = false;
        })
        .catch(err => {
          this.error = true;
          this.isLoading = false;
        });
      this.$store.commit("SET_NOTEREFRESH", false);
    },
    showDetail(item) {
      this.$router.push({
        path: "/noteDetail",
        query: {
          title: item["SNTtile"],
          content: item["SNContent"],
          createTime: item["CreateTime"]
        }
      });
    },
    dataDelete(e, item) {
      e.stopPropagation();
      Dialog.confirm({
        message: "确认删除此数据？"
      })
        .then(() => {
          this.$gatewayHttp.note
            .postDelete({ snid: item["SNID"] })
            .then(res => {
              if (res.Result) {
                this.onSearch();
              } else {
                this.$toast("删除失败");
              }
            })
            .catch(err => {
              this.$toast("删除失败");
            });
        })
        .catch(() => {});
    },
    onSearch() {
      this.error = false;
      this.finished = false;
      this.pno = 0;
      this.total = 0;
      this.dataList = [];
      this.getDataList();
    }
  },
  computed: {
    searchData() {
      if (!this.value) {
        return this.dataList;
      } else {
        return this.dataList.filter(
          r =>
            r["SNTtile"].indexOf(this.value) > -1 ||
            r["SNContent"].indexOf(this.value) > -1
        );
      }
    }
  },
  watch: {
    "$store.getters.noteRefresh"(newVal, oldVal) {
      if (newVal !== oldVal && newVal) {
        this.onSearch();
      }
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
#note {
  .head-back {
    display: none;
  }

  .search-input {
    position: fixed;
    width: 100%;
    top: 44px;
    z-index: 9;
    /deep/ .van-search {
      position: relative;
      &:after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: #ccc;
        transform: scaleY(0.3);
      }
    }
  }

  .pull_fresh {
    padding-top: 54px;
    background: #fff;
  }

  .main {
    padding: 7px 0;
    .item {
      position: relative;
      padding: 5px 16px;
      margin-bottom: 6px;
      text-align: left;
      background-color: #fff;

      &:after {
        content: " ";
        position: absolute;
        margin: 0 10px;
        width: calc(100% - 20px);
        height: 1px;
        left: 0;
        bottom: 0;
        background-color: #ccc;
        transform: scaleY(0.3);
      }
      h3 {
        font-size: @font-sub-title;
        margin: 0;
        line-height: 2.5;
      }

      .item_operate {
        position: absolute;
        top: 3px;
        right: 2vw;
        padding: 10px;
      }

      img {
        width: 160px;
        height: 113px;
      }
      /deep/ .ql-editor {
        padding: 0;
        background: #fff;
        color: #b2bac2;
        font-size: @font-info;

        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        //这个代表你要在几行显示省略号
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        word-break: break-all;
        min-height: 10px;
        max-height: 5 * 1.4 * @font-info;
      }
      .time {
        padding-top: 15px;
        font-size: @font-sub-title;
        color: #888;
      }
    }

    .list_end {
      text-align: center;
      padding: 10px;
      color: #2c3e50;
      font-size: @font-sub-title;
    }
  }
}
</style>


